import ApiMovie from './serviseAPI';
import { apiMovie } from './serviseAPI';
//console.log(apiMovie);
//  

const PAGE_KEY = 'page'; //  
const TOTAL_KEY = 'total_pages'; //   
const CURRENT_KEY = 'current'; //   
const WATCHED_KEY = 'wathedArr'; //   
const QUEUE_KEY = 'queueArr'; //     
const GENRES_KEY = 'genres'; //  

// value  JSON     key locaStorage
const save = (key, value) => {
  try {
    // console.log("from save try")
    const serializedState = JSON.stringify(value); //value   JSON ,     ""
    localStorage.setItem(key, serializedState); // localStorage   key   serializedState
  } catch (error) {
    console.error('Set state error: ', error.message);
  }
};
//  locaStorage key    JS
const load = key => {
  try {
    // console.log("from load try");
    const serializedState = localStorage.getItem(key); // value  key  localStorage
    return serializedState === null ? undefined : JSON.parse(serializedState); //  value  JS 
  } catch (error) {
    console.error('Get state error: ', error.message);
  }
};
// key  locaStorage
const remove = key => {
  try {
    return localStorage.removeItem(key); //   key   localStorage
  } catch (error) {
    console.error('Get state error: ', error.message);
  }
};

if (!load(CURRENT_KEY)) {
  save(CURRENT_KEY, []);
}
if (!load(WATCHED_KEY)) {
  save(WATCHED_KEY, []);
}
if (!load(WATCHED_KEY)) {
  save(QUEUE_KEY, []);
}

//       
// load,save,remove  storage.

// index  submit         localStorage
function saveGenres(genres) {
  //*************
  //     localStorage        GENRES
  save(GENRES_KEY, genres);
}
//saveGenres(); //   localStorage  
function loadGenres() {
  return load(GENRES_KEY);
}
//loadGenres();
//     current  localStorage        
function saveTrendMovies(page) {
  remove(CURRENT_KEY);
  apiMovie
    .fetchAllMovie(page)
    .then(data => {
      // console.log("data from saveTrendMovies page ",page,data.results);
      save(CURRENT_KEY, data.results);
    })
    .catch(error => console.log(error.message));
}
//saveTrendMovies(1);
//        current localStorage
function loadTrendMovies() {
  const movies = load(CURRENT_KEY);
  // console.log("from storage.loadTrendMovies",movies);
  return movies;
}
//loadTrendMovies();

// ******     ,   
function saveTotalPages(number) {
  save(TOTAL_KEY, number);
}
//  
function loadTotalPages() {
  load(TOTAL_KEY);
}

//
function savePage(page) {
  save(PAGE_KEY, page);
}

function loadPage() {
  load(PAGE_KEY);
}

//  20   localStorage*******
function saveCurrentPage(data) {
  remove(CURRENT_KEY);
  save(CURRENT_KEY, data); //???data.results
}
//      localStorage
function loadCurrentPage() {
  const arrayMovies = load(CURRENT_KEY);
  console.log('from loadCurrentPage', arrayMovies);
  return arrayMovies;
}

//     WATCHED
//       localStorage(watched)
function loadFromWatched() {
  const movies = load(WATCHED_KEY);
  return movies;
}

//   QUEUE  (   )
//       localStorage(queue)
function loadFromQueue() {
  const movies = load(QUEUE_KEY);
  return movies;
}

//      ,  import  storage from './storage'
//     : storage.saveGenres()/storage.loadGenres()......
export default {
  save, //storage.save(key,value)   key value(JSON) /  setItem/stringify/try/catch
  load, //storage.load(key)  JS  key /  getItem/parse/try/catch
  remove, //storage.remove(key)  key /  removeItem/try/catch
  saveGenres, //storage.saveGenres()           
  loadGenres, //storage.loadGenres()     
  saveTotalPages, //storage.saveTotalPages    
  loadTotalPages, //storage.loadTotalPages    
  savePage,
  loadPage,
  saveTrendMovies, //storage.saveTrendMovies(page)          
  loadTrendMovies, //storage.loadTrendMovies()          
  saveCurrentPage, //storage.saveCurrentPage(data)         (data)  
  loadCurrentPage, //storage.loadCurrentPage()     ,     
  loadFromWatched, //storage.loadFromWatched()   WATCHED      MyLibrary
  loadFromQueue, //storage.loadFromQueue()   QUEUE      MyLibrary
};
